footer{
background: var(--dark);
width: 100%;
bottom: 0;
left:0;
margin:0 auto;
height: 30vh;
position: inherit;
}

.footer-wrapper{
display: flex;
height: 100%;
align-items: center;
margin: 0;
padding: 2rem 2rem 0 2rem;
} 

.footer-container{
display: flex;
justify-content: space-evenly;
align-items: flex-start;
flex-direction: row;
flex-wrap: wrap;
width:100%;
text-align: left;
margin-bottom: 2rem;
}

.footer-links{
width: 20%;
margin: 1rem;
display: flex;
justify-content: flex-start;
flex-direction: column;
color: var(--light);
}

.footer-links h4{
font-size: 1.5rem;
line-height: 15px;
margin:0 0 .8rem 0;
cursor:pointer;
color: var(--yellow);
} 

.footer-links a{
font-size: 1.3rem;
letter-spacing: 1px;
margin:0.2rem 0;
cursor:pointer;
color: rgb( 175 175 179);
text-decoration: none;
}

a:hover{
color: var(--accent);
}

.media{
display: flex;
flex-direction: row;
gap: 0.6rem;
}

.media a{
  margin-top: .5rem;
}

.media-icon{
  height: 45px;
  width: 45px;
  cursor: pointer;
}

.media-icon:hover{
  color: var(--accent);
}

@media screen and (max-width: 1200px){

  .footer-links h4{
    font-size: 1.3rem;
    }

  .footer-links a {
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 992px){
.media-icon{
  height: 25px;
  width: 25px;
}
footer{
  height: 40vh;
  padding:2rem;
}
.sb-footer-heading h4{
  font-size: 2rem;
}
.footer-links{
  width: 200px;
  margin: 1rem;
}
.media{
  flex-direction: row;
  width: 450px;
}
}


@media screen and (max-width: 768px) {

footer{
  height: 40vh;
  padding:1.8rem;
}
.sb-footer-heading h4{
  font-size: 1.6rem;
}
.footer-links{
  width: 200px;
  margin: 1rem;
}
.media{
  flex-direction: row;
  width: 450px;
}
.media-icon{
  height: 25px;
  width: 25px;
}
}

@media screen and (max-width: 576px) {

footer{
  height: 60vh;
  padding:1rem;
  justify-content: center;
  align-items: center;
}
.sb-footer-heading h4{
  font-size: 2rem;
  line-height: 42px;
}
.footer-links{
  margin: 1rem 0;
}
.sb-footer-btn a{
  font-size: 14px;
  line-height: 20px;
}
}


@media screen and (max-width: 400px) {

  footer{
  height: 60vh;}

.sb-footer-heading h4{
  font-size: 27px;
  line-height: 38px;
}
}